import {IndexModel} from "../../model";
const  indexModel = new IndexModel()
export default {
    name: "repair",
    data(){
        return {
            list: [],
            page:1,
            size:10,
            sumPrice:"0.00",
            sumData:[]
        }
    },
    onLoad(){
        this._getRepair()
    },
    methods:{
        _submit(){
            let data = JSON.stringify(this.sumData)
            uni.navigateTo({
                url:"/pages/car_wash_buyOrder/car_wash_buyOrder?data="+ data
            })
        },
        _getRepair(){
            let data = {
                page:this.page,
                size:this.size
            }
            indexModel.getRepair(data).then((res)=>{
                if(res.data) {
                    this.list = res.data.data
                }
            })
        },
        //计算价格
        _getPrice(data){
            let price = 0
            let sumData = []
            for (let item of data) {
                if(item.checked) {
                    let items = {
                        id:item.one_sku.id,
                        num:1
                    }
                    sumData.push(items)
                    price+=parseFloat(item.min_price)
                }
            }
            this.sumData = sumData
            this.sumPrice = price.toFixed(2)
        },
        radioChange(id){
            let list = this.list
            list.map((item)=>{
                if(item.id == id) {
                    item.checked = !item.checked
                }
            })
            this._getPrice(list)
            this.list = JSON.parse(JSON.stringify(list))
        }
    }
}
